import { SearchResultService } from './../services/search-result.service';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Injectable } from '@angular/core';
import { Response, RootObject } from '../search-result';


@Component({
  selector: 'search-form',
  templateUrl: './search-form.component.html',
  styleUrls: ['./search-form.component.css']
})

@Injectable()
export class SearchFormComponent {
  responseList: Response[] = [];

  constructor(private service: SearchResultService) { }

  submit(f: NgForm) {
    console.log('submit');
    let mySearchText = f.value.searchText;
    mySearchText = mySearchText.replace(' ', '%20');

    this.service.getResults(mySearchText)
      .subscribe((data: RootObject) => {
        this.responseList = data.Response
      })
  }
}




