import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { RootObject } from '../search-result';

@Injectable({
  providedIn: 'root'
})
export class SearchResultService {
  private myUrl = "https://w4qxpmz4e0.execute-api.us-east-1.amazonaws.com/dev/search/"

  constructor(private http: HttpClient) { }

  getResults(mySearchText: string) {
    return this.http.get<RootObject>(this.myUrl + mySearchText);
  }
}
